import Prompt from '@/components/Prompt/Prompt'

export default {
  components: { Prompt },
  props: ['articleList', 'recordList', 'loading'],
  data () {
    return {
      TabCur: 0,
      scrollLeft: 0,
      emptyList: [1, 2, 3],
      tagList: [
        // { name: '推荐' },
        { name: '打卡' },
        { name: '文章' }
      ]
    }
  },
  methods: {
    tabSelect (e) {
      this.TabCur = e.currentTarget.dataset.id;
      this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
    },
    toArticleDetail (item) {
      uni.showModal({
        showCancel: false,
        title: '抱歉!',
        content: '文章的内容正在迁移中, 请先前往该公众号查看'
      })
      // uni.navigateTo({
      //   url: `/pages/article/detail?id=${item.id}`
      // })
    },
    toRecordDetail (item) {
      uni.navigateTo({
        url: `/pages/record/detail?recordId=${item.id}`
      })
    }
  }
}
